@import url("https://fonts.googleapis.com/css2?family=Spartan:wght@700&display=swap");
body {
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: hsl(222, 26%, 31%);
  font-family: "Spartan", sans-serif;
  font-weight: 700;
}
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}
.calc {
  display: flex;
  flex-direction: column;
}
form {
  width: 100%;
}
input {
  text-align: right;
  background-color: hsl(224, 36%, 15%);
  margin-bottom: 10px;
  border: none;
  border-radius: 4px;
  width: 100%;
  padding: 10px;
  font-size: x-large;
}
.theme {
  display: flex;
  flex-direction: column;
  padding-left: 10px;
}
.bottom {
  display: flex;
  height: 10px;
  border-radius: 30px;
  background-color: hsl(223, 31%, 20%);
}
.top span {
  margin-right: 5px;
}
.check {
  background-color: hsl(6, 63%, 50%);
  height: 10px;
  width: 10px;
  border-radius: 50%;
}
.right {
  display: flex;
  align-items: center;
  justify-content: center;
}
.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 15px;
  background-color: hsl(223, 31%, 20%);
  padding: 10px;
  border-radius: 4px;
  width: 100%;
}
button {
  border: transparent;
  padding: 5px 10px;
  border-radius: 5px;
  background-color: hsl(30, 25%, 89%);
  box-shadow: 0px 3px hsl(28, 16%, 65%);
}
.rem {
  background-color: hsl(225, 21%, 49%);
  box-shadow: 0px 3px hsl(224, 28%, 35%);
}
.reset {
  grid-column: 1/3;
}
.equate {
  grid-column: 3/5;
  background-color: hsl(6, 63%, 50%);
  box-shadow: 0px 3px hsl(6, 70%, 34%);
}
